import { Block } from '@ali/4ever-cangjie';
import {
  JsonML,
  getTagName,
  getAttributes,
} from '@ali/4ever-utils';
import Embed from '../models';

export default {
  name: 'embed',

  match(node: JsonML): boolean {
    return getTagName(node) === 'embed';
  },

  convert(node: JsonML): Block {
    return Embed.create({
      data: getAttributes(node) || {},
    });
  },
};
